@import '../../../../../base';

.list-view-item-details-container {
  .inner-container {
    padding: 10px 18px;

    @media screen and (min-width: 768px) {
      padding: 20px 40px;
    }
  }

  hr {
    margin-bottom: 0;
  }
  
  td,
  a,
  h6,
  p,
  span {
    font-size: $font-size-tiny;
    font-weight: $font-weight-bold;

    @media screen and (min-width: 768px) {
      font-size: $font-size-smallest;
    }
  }

  .table-container {
    @media screen and (min-width: 768px) {
      &:nth-of-type(1) {
        padding-right: 20px;
      }
  
      &:nth-of-type(2) {
        padding-left: 20px;
      }
    }
  }

  td {
    padding: 10px 0 0 0;
  }

  .title {
    color: $color-input-text;
    margin-bottom: 10px;
  }

  .desc {
    font-weight: $font-weight-normal;
    color: $color-input-form-landing-desc2;
  }

  .value {
    color: $color-input-form-landing-desc2;
    text-align: right;
  }

  .fa-check {
    color: $color-input-form-landing-desc;
  }

  .fa-times {
    color: $color-error-alt-3;
  }
}
