@import '../../../../base';

$left-container-width: 220px;
$left-container-offset: 34px;

$container-margin: 40px;
$container-resp-margin: 15px;

.listview-container {
  max-width: 254px + 854px + $container-margin + $container-margin;
  margin: auto;
  margin-bottom: 150px;
  position: relative;
  margin-top: 52px;


  padding: 0 $container-margin 0 $container-margin !important;
  @media screen and (max-width: 576px) {
    padding: 0 $container-resp-margin 0 $container-resp-margin !important;
  }

  @media screen and (max-width: 991px) {
    flex-direction: column;
    margin-top: 0;
  }

  .left-container {
    left: 40px;
    position: absolute;
    width: $left-container-width !important;
    padding-top: 62px;
    margin-right: $left-container-offset;
    // position: fixed;
    // transform: translateX(-$left-container-width - $left-container-offset);

    @media screen and (max-width: 991px) {
      left: 0;
      position: relative;
      margin-right: 0;
      padding-top: 0;
      width: 100% !important;
      margin-top: 0 !important;
    }

    .main-promo {
      max-width: 100%;
    }

    .input-wrapper-main-v3 {
      margin-top: 0;
      padding: 0;

      @media screen and (max-width: 991px) {
        padding-right: 0;
        padding-left: 3px;
      }

      .dropdown-container {
        background-color: $product-background;
      }

      .icon-container {
        margin-left: 5px;
        img {
          filter: none;
        }
      }

      .title-header {
        padding-left: 15px !important;
        transform: translateY(8px) !important;

        font-weight: $font-weight-normal !important;
        font-size: $font-size-extra-tiny !important;
        color: $color-input-form-landing-desc2 !important;
      }
    }

    .plantype-drop {
      @media screen and (max-width: 991px) {
        .input-wrapper-main-new {
          padding-right: 3px;
          padding-left: 0;
        }
      }

      .back-list-button {
        display: flex;
        justify-content: center;
        align-items: center;

        margin-bottom: 16px;
        height: 48px;
        border: 1px solid $color-input-form-landing-desc2;
        box-sizing: border-box;
        border-radius: 12px;
        width: 100%;

        font-weight: $font-weight-bold;
        font-size: $font-size-smallest;

        color: $color-input-form-landing-desc2;

        transition: background-color 0.3s ease, color 0.3s ease;

        @media screen and (max-width: 991px) {
          margin-left: 3px;
          position: relative;
          font-size: $font-size-tiny;
        }

        img {
          filter: $color-filter-hover;
          transform: rotate(90deg);
          margin-right: 10px;
        }

        &:hover {
          background-color: $color-input-form-landing-desc2;
          color: white;
        }
        &:active {
          transform: scale(1.05);
          @media screen and (max-width: 991px) {
            transform: scale(1.05);
          }
        }
      }

      .title-header {
        padding-left: 44px !important;
        // transform: translateY(9px) !important;
      }

      .selected-text {
        padding-top: 13px;
        // padding-left: 0px !important;
      }
    }

    .tier-selection-checkbox-container {
      background: white;
      box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.05);
      border-radius: 20px;
      padding: 20px;

      .detail-value {
        font-weight: bold;
        font-size: 14px;

        color: #2C3652
      }

      .tier-header {
        margin: 0;
        display: flex;
        p {
          flex: 1;
        }
      }

      h6 {
        font-weight: normal;
        font-size: 10px;

        color: #6E7F95;
        transform: translateY(-3px);
      }

      .options-container {
        display: flex;
        flex-direction: column;
        align-items: flex-start;

        .option {
          display: flex;
          align-items: center;

          .option-button {
            position: relative;
            margin: 7px 10px 7px 0;
            width: 16px;
            height: 16px;
            background-color: #6480EB;
            // border-radius: 20px;
            border: 1px solid #CADBEA;
            box-sizing: border-box;
            border-radius: 20px;

            &:before {
              content: '';
              width: 100%;
              height: 100%;
              background-color: white;
              position: absolute;
              border-radius: 50%;
              top: 0;
              left: 0;
              transition: transform 0.3s ease;
            }

            &.selected {
              border: none;
              &:before {
                transform: scale(0.4);
              }
            }
          }
        }
      }
    }

    .filter-container {
      input {
        max-width: 80%;
        text-overflow: ellipsis;
      }
    }
  }

  .fade-container {
    animation-duration: 0.3s;
  }

  .right-container {
    max-width: calc(100% - 254px);
    width: 854px;
    margin-left: 254px;
    min-height: 500px;
    @media screen and (max-width: 991px) {
      margin-left: 0;
      max-width: 100%;
      width: 100%;
    }

    .piggy-list-container {
      display: inline-block;
      @media screen and (max-width: 991px) {
        // display: flex;
        margin-bottom: 10px;
        width: 100%;
        // justify-content: flex-end;
        .piggy-container-new {
          // margin: 0;
        }
      }
    }

    .top-right-container {
      margin: 0 0 16px 0;
      width: 100%;

      .buttons {
        flex: 1;
      }
  
      %active-button {
        background-color: $color-input-light-blue !important;
      }
  
      .coverage-button {
        max-width: 150px;
        margin-bottom: 5px;
        
        margin-right: 30px;
        height: 48px;
        border: none;
  
        background: transparent;
        border-radius: 50px;
  
        font-weight: $font-weight-bold;
        font-size: $font-size-tiny;
        text-align: center;
  
        color: $color-input-text;

        transition: background-color 0.3s ease;
  
        &:hover {
         @extend %active-button;
        }
  
        &:active,
        &:focus {
          outline: none;
          box-shadow: none;
          color: $color-input-text;
        }
      }
  
      .coverage-active-button {
        @extend %active-button;
      }
    }

    .list-item-wrapper {
      background-color: white;

      .list-item {
        opacity: 1;
        transition: opacity 0.2s ease-in-out;
      }

      .list-item-fade {
        opacity: 0;
      }
    }
    
    

    .sub-promo {
      width: 100%;
      margin-bottom: 8px;
      background-color: $color-input-light-blue !important;
      justify-content: center;
      align-items: center;
      img {
        max-width: 100%;
        margin: auto;
        max-height: 200px;
      }
    }
  }
}
