@import '../../../base';

.product-detail-container {
  @media screen and (max-width: 767px) {
    .scroll-tabs {
      margin-top: 0;
    }
  }

  .tag-label-margin {
    margin-top: 10px;
  }

  .prex-text {
    font-weight: $font-weight-normal;
    font-size: $font-size-smallest;
    color: $color-input-text;
    margin-bottom: 20px;

    @media screen and (max-width: 767px) {
      padding: 15px;
      margin-bottom: -10px;
    }
  }

  .segment {
    @media screen and (min-width: 768px) {
      background: white;
      /* Mild shadow */
      box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.05);
    }
    
    margin-bottom: 10px;
  }

  .product-header-container-wrapper {
    // margin-bottom: 48px;
  }

  .product-header-container {
    display: flex;
    flex: 1;

    .product-logo {
      width: 122px;
      min-height: 97px;
      margin-right: 10px;
      display: flex;
      align-items: flex-start;

      img {
        width: 100%;
        height: auto;
        padding-bottom: 10px;
      }
    }

    .product-name-container {
      display: flex;
      flex-wrap: wrap;
      flex-direction: column;
      justify-content: center;
      width: 100%;

      @media screen and (max-width: 767px) {
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;
        padding: 10px 0 10px 20px;
      }

      h2 {
        font-weight: $font-weight-heavy;
        @media screen and (max-width: 767px) {
          margin: 0 10px 0 0;
        }
      }

      
      .discount-container {
        background-color: $color-error-alt-3;
        border-radius: 4px;
        height: 32px;
        width: 102px;
        display: flex;
        justify-content: center;
        align-items: center;

        p {
          margin: 0;
          font-weight: $font-weight-bold;
          font-size: $font-size-tiny;
          color: $product-white;
        }
      }

      .hidden {
        background-color: transparent;
      }
    }
  }

  .price-container {
    display: flex;
    align-items: flex-start;

    .price-inner-container {
      display: flex;
      .price-value-container {
        margin-right: 18px;
        .net-price {
          font-weight: $font-weight-heavy;
          margin: 0;
        }
      }
    }
  }

  .price {
    float: right;
    font-weight: $font-weight-bold;
    font-size: $font-size-tiny;
    text-decoration-line: line-through;
    color: $color-input-form-landing-desc2;
  }

  .cashback-earned-container {
    margin: auto;
    span {
      color: $color-input-text;
    }
  }

  .banner-container {
    display: flex;
    justify-content: center;
    position: relative;

    .banner {
      width: calc(100% + 30px);

      img {
        width: calc(100% + 30px);
        transform: translateX(-15px);
        height: auto;
      }

      @media screen and (min-width: 769px) {
        width: calc(100%);

        img {
          width: calc(100%);
          transform: none;
        }
      }
    }

    .bottom-banner {
      position: absolute;
      bottom: 0;
      left: 0;

      .banner-logo {
        background: white;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.1);
        border-radius: 20px;
        width: 80px;
        height: 80px;
        transform: translateY(0px);
  
        img {
          width: 100%;
          height: auto;
        }
      }
  
      h5 {
        font-weight: $font-weight-heavy;
        font-size: $font-size-tiny;
        color: $color-input-text;
        transform: translateY(4px);

        @media screen and (min-width: 768px) {
          transform: translateX(15px) translateY(-5px);
          font-size: $font-size-smallest;
        }
      }
    }
  }

  .feature-container {
    display: flex;
    padding: 25px;

    @media screen and (max-width: 767px) {
      padding-bottom: 0;
    }

    .image {
      margin-right: 20px;
      @media screen and (max-width: 767px) {
        width: 24px;
        transform: translateY(-5px);

        img {
          width: 24px;
          height: auto;
        }
      }
  
      @media screen and (min-width: 768px) {
        width: 60px;
        transform: translateY(15px);
      }
    }

    h4 {
      font-weight: $font-weight-heavy;
      font-size: $font-size-smallest;

      color: $color-input-text;

      @media screen and (max-width: 767px) {
        color: $font-size-smallest;
        margin: 0;
      }
    }

    p {
      font-weight: $font-weight-normal;
      font-size: $font-size-tiny;

      color: $color-input-form-landing-desc2;

      @media screen and (max-width: 767px) {
        font-weight: $font-weight-semibold;
        font-size: $font-size-smallest;

        color: $color-input-text;
      }
    }
  }

  .highlight-container {
    padding: 33px;

    .highlight-title-container {
      display: flex;
      justify-content: center;

      .title {
        flex: 1;

        font-weight: $font-weight-normal;
        font-size: $font-size-tiny;

        color: $color-input-form-landing-desc2;
      }

      .highlight-tooltip {
        padding-left: 18px;
        // transform: translateY(-5px);
      }

      .tooltip-hidden {
        filter: brightness(100);
      }
    }

    .value {
      font-weight: $font-weight-heavy;
      font-size: $font-size-small + $font-size-tiny;

      color: $color-input-text;
    }
  }

  .coverage-container {
    padding: 40px;

    .coverage-inner-container {
      box-sizing: border-box;
      padding-top: 18px;

      .content-bold {
        font-weight: $font-weight-bold !important;
        font-size: $font-size-smallest !important;
        color: $color-input-text !important;
      }
  
      .coverage-header {
        display: flex;
        align-items: flex-start;
  
        img {
          margin-right: 10px;
        }
  
        h5 {
          font-size: $font-size-regular !important;
          color: $color-input-text;
          padding-right: 10px;
        }
      }
    }
  }

  .content-container {
    padding-bottom: 18px;
    
    @media screen and (max-width: 767px) {
      background: $product-resp-blue;
      border-radius: 25px;
      padding: 15px;
    }
   
    button,
    a {
      float: right;
      color: $color-hover !important;

      &:hover {
        text-decoration: underline;
      }
    }

    .content,
    .content-sub {
      // display: flex;
      // align-items: center;
      // justify-content: center;

      .title,
      p {
        font-weight: $font-weight-normal;
        font-size: $font-size-tiny;
        color: $color-input-form-landing-desc2;
        margin: 0;
      }

      .title {
        text-align: left;
      }

      p {
        text-align: right;
      }
    }

    .content {
      .title {
        font-weight: $font-weight-bold;
      }

      p {
        @media screen and (max-width: 767px) {
          font-weight: $font-weight-bold;
        }
      }
    }

    .content {
      margin-bottom: 5px;
    }

    .content-sub {
      margin: 5px 0;
      @media screen and (max-width: 767px) {
        margin: 3px 0;
      }
    }

    .content-padding {
      padding-bottom: 18px;
    }

    .content-break {
      margin-bottom: 15px;
      background: $color-input-outline;
      width: 100%;
      height: 1px;
    }

    .fa-check {
      color: $color-input-form-landing-desc;
    }
  
    .fa-times {
      color: $color-error-alt-3;
    }
  }

  .content-border {
    border-bottom: 1px solid $color-input-outline;
  }

  .no-bottom-padding {
    padding-bottom: 0;
  }

  .content-sub-container {
    list-style-type: none;
    padding-left: 0;
    @media screen and (min-width: 768px) {
      padding-left: 20px;
    }
  }

  .content-sub-container-padding {
    padding-bottom: 16px;
  }

  .scroll-tabs {
    background: #FFFFFF;
    box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.1);
    border-radius: 15px;
    border: none;
  }

  .coverage-resp-container {
    background: white;
    border: 1px solid $color-input-outline;
    border-radius: 25px;
    padding: 0;

    .coverage-resp-main-title {
      display: flex;
      align-items: center;
      padding: 20px;

      .coverage-resp-img {
        width: 22.5px;
        height: 22.5px;
        object-fit: contain;

        img {
          width: 100% !important;
          height: auto;
          max-height: 100%;
        }
      }

      h4 {
        font-weight: $font-weight-bold;
        font-size: $font-size-small;

        color: $color-input-form-landing-desc2;
        margin: 0;
        margin-left: 10px;
      }
    }
  }

  .document-container {
    display: flex;
    width: 100%;
    align-items: center;
    background-color: white;
    border: 1px solid $color-input-outline;
    border-radius: 25px;
    padding: 17px 25px;
    margin: 5px 0;
    transition: background-color 0.3s ease, transform 0.1s ease;

    &:hover {
      background-color: $color-input-form-landing-desc;
      img {
        filter: brightness(100) saturate(100%) !important;
      }
      .document {
        p {
         color: white;
        }
      }
    }

    &:active {
      transform: scale(1.05);
    }

    .document {
      display: flex;
      flex: 1;

      p {
        margin: 0;
        margin-left: 13.5px;
        font-weight: $font-weight-bold;
        font-size: $font-size-small;

        color: $color-input-form-landing-desc2;
      }
    }

    .arrow {
      img {
        filter: $color-filter-input-form-landing-desc2;
      }
    }
  }

  .product-price-resp-outer-container {
    position: fixed;
    height: 80px;
    bottom: 0;
    left: 0;
    right: 0;
    background: white;
    box-shadow: 0px -3px 10px rgba(0, 118, 215, 0.2);
    transform: translateY(80px);
    opacity: 0;
  }

  .product-price-resp-container {
    position: fixed;
    padding: 10px 24px;
    z-index: 100;
    bottom: 0;
    width: 100%;
    max-width: 854px;
    display: flex;
    height: 80px;
    transform: translateY(80px);
    opacity: 0;
    transition: transform 0.2s ease, opacity 0.3s ease;

    @media screen and (max-width: 1200px) {
      left: 0;
      padding: 10px 80px;
      max-width: none;
    }

    @media screen and (max-width: 992px) {
      left: 0;
      padding: 10px 40px;
      margin: auto;
      padding-right: 15px * 2;
    }

    @media screen and (max-width: 576px) {
      padding: 10px 20px;
    }

    .price-container {
      flex: 1;
      display: flex;
      flex-direction: column;
      align-items: flex-start;

      h5 {
        font-weight: $font-weight-bold;
        font-size: $font-size-small;
        color: $color-input-form-landing-desc2;
      }

      h6 {
        font-weight: $font-weight-normal;
        font-size: $font-size-tiny;

        color: $color-input-form-landing-desc2;
      }

      h4 {
        font-weight: $font-weight-heavy;
        font-size: $font-size-big;

        color: $color-input-text;
        display: flex;
        align-items: center;

        .price {
          margin-left: 5px;
        }
      }

      h4,
      h5,
      h6 {
        margin: 0;
      }
    }
  }
}

.bottom-opened-outer {
  transform: translateY(0) !important;
  opacity: 1 !important;
  transition: transform 0.2s ease, opacity 0.2s ease;
}

.bottom-opened-inner {
  transform: translateY(0) !important;
  opacity: 1 !important;
  transition: transform 0.4s ease, opacity 0.8s ease;
}

.product-detail-error {
  max-width: 600px;

  .modal-body {
    p {
      text-align: center;
      font-weight: $font-weight-bold;
      font-size: $font-size-regular;
      color: $color-input-text;
    }
  }

  .modal-footer {
    display: flex;
    justify-content: center;

    button {
      min-width: 150px !important;
      max-width: 100%;
    }
  }
}
