@import '../../../../base';

.header-list-view-container {
  $horizontal-padding: 39px;
  $horizontal-padding-resp: 15px;

  max-width: 854px;
  margin-bottom: 8px;
  padding: 15px 0 0 0;
  @media screen and (max-width: 767px) {
    padding-top: 10px;
  }
  @media screen and (max-width: 991px) {
    max-width: none;
  }

  background: #FFFFFF;
  /* Mild shadow */
  box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.05);

  .info-container {
    .compare-container {
      .compare-price {
        padding-left: $horizontal-padding-resp;
        @media screen and (max-width: 767px) {
          margin: 10px 0 5px 0;
        }
      }

      .compare {
        margin-left: 20px;
        display: inline-block;

        @media screen and (max-width: 767px) {
          margin-left: 6px;
          display: block;
        }
      }

      .compare-extra {
        margin-left: $horizontal-padding;
        @media screen and (max-width: 767px) {
          margin-left: $horizontal-padding-resp;
        }
      }
    }

    .insur-container {
      padding-left: $horizontal-padding;
      @media screen and (max-width: 767px) {
        padding-left: $horizontal-padding-resp;
        margin-bottom: 5px;
      }

      display: flex;
      align-items: center;

      .logo {
        width: 60px;
        height: 60px;
        object-fit: contain;
        margin-right: 13px;

        @media screen and (max-width: 767px) {
          width: 50px;
          height: 50px;
        }
      }

      .header-inner {
        position: relative;
        @media screen and (max-width: 767px) {
          width: 100%;
          align-items: baseline;
          display: flex;
        }

        button {
          padding-right: 15px;
          transition: transform 0.3s ease;
          color: $color-input-form-landing-desc;
          
          font-weight: $font-weight-bold;

          @media screen and (min-width: 769px) {
            position: absolute;
            width: 100px;
            bottom: 1px;
          }
  
          &:hover {
            transform: scale(1.1);
          }
  
          p {
            font-size: $font-size-smallest;
            margin: 0;
            text-align: right;
          }
        }
  
        h2 {
          position: relative;
          display: inline-block;
          font-weight: $font-weight-bold;
          font-size: $font-size-big;
  
          color: $color-input-text;
          margin-right: 5px;
          margin-bottom: 0;
  
          transition: color 0.3s ease;
  
          @media screen and (max-width: 767px) {
            flex: 1;
            font-size: $font-size-small;
          }
        }
      }
    }

    .detail-container {
      padding-left: $horizontal-padding;
      @media screen and (max-width: 767px) {
        padding-left: $horizontal-padding-resp;
      }

      .detail-inner-wrapper {
        &:not(:last-of-type) {
          margin-right: 40px;
        }
      }

      .detail-inner {
        display: flex;
        flex-grow: 1;

        @media screen and (max-width: 767px) {
          margin-right: $horizontal-padding-resp;
        }

        .detail-title {
          // max-width: 85px;
          font-weight: $font-weight-normal;
          font-size: $font-size-tiny;
  
          color: $color-input-form-landing-desc2;

          @media screen and (max-width: 767px) {
            max-width: none;
          }
        }
  
        .detail-value {
          font-weight: $font-weight-heavy;
          font-size: $font-size-regular;
  
          color: $color-input-form-landing-desc2;

          @media screen and (max-width: 767px) {
            text-align: end;
            flex: 1;
  
            font-size: $font-size-tiny !important;
          }
        }

        img {
          margin-left: 20px;
        }
      }
    }
  }

  .detail-price-container {
    flex-grow: 1;
  }

  .price-container {
    @media screen and (max-width: 767px) {
      padding-right: $horizontal-padding-resp;
    }

    display: flex;
    justify-content: flex-end;

    .button-container {
      @media screen and (min-width: 768px) {
        padding-right: $horizontal-padding;
      }
    }

    button {
      margin-bottom: 5px;

      @media screen and (max-width: 430px) {
        p {
          font-size: $font-size-smallest;
        }
      }
    }

    span {
      font-weight: $font-weight-semibold;
      font-size: $font-size-tiny;

      color: $color-input-form-landing-desc;
      margin-left: 5px;

      @media screen and (max-width: 430px) {
        font-size: $font-size-small * 0.5;
      }
    }

    .compare-padding {
      padding-right: 15px;
    }
  }

  .non-resp-compare-reco {
    @media screen and (min-width: 768px) {
      display: flex;
      justify-content: flex-end;
      align-items: flex-start;
      margin-bottom: 60px;
    }
  }

  .promo-tag {
    padding: 7px 6px;
    background: #FFD12E;
    border-top-left-radius: 4px;
    border-bottom-left-radius: 4px;
    color: white !important;
    font-size: $font-size-tiny;
    display: flex;
    align-items: center;
    float: right;
    height: 100%;

    @media screen and (max-width: 767px) {
      padding: 6px 2px;
      font-size: $font-size-small * 0.5;
      border-top-left-radius: 0;
      border-bottom-left-radius: 0;
      border-top-right-radius: 4px;
      border-bottom-right-radius: 4px;
    }

    @media screen and (min-width: 768px) {
      margin-bottom: -10px;
    }
  }

  .total {
    font-weight: $font-weight-normal;
    font-size: $font-size-extra-tiny;

    color: $color-input-form-landing-desc2;
    margin: 10px 0 0 0 ;
    line-height: 5px;
  }

  .price-non-resp {
    flex-direction: column;
    align-items: flex-end;
    padding-right: 32px;
    transform: translateY(5px);
  }

  .net-price {
    font-weight: $font-weight-heavy;
    font-size: $font-size-extra-tiny * 3;

    color: $color-input-text;
    line-height: 30px;

    @media screen and (min-width: 768px) {
      margin-bottom: 11px;
    }
  }

  %price-value {
    font-weight: $font-weight-bold;
    font-size: $font-size-extra-tiny;
  }

  .price {
    font-size: $font-size-extra-tiny;
    margin: 0;
    span {
      @extend %price-value;

      &:nth-of-type(1) {
        margin: 0;
        background: $color-error-alt-3;
        border-radius: 4px;
        color: white;
        padding: 0 7px;
        @media screen and (max-width: 767px) {
          margin-right: 6px;
        }
      }

      &:nth-of-type(2) {
        text-decoration-line: line-through;
        color: $color-input-form-landing-desc2;
      }
    }
  }

  .cashback-earned-container {
    display: flex;
    align-items: center;
    p {
      margin: 0;
      text-align: center;
      span {
        color: $color-input-text;
      }

      @media screen and (max-width: 430px) {
        line-height: 0.5rem;
        span {
          font-size: 9px;
        }
      }
    }
    
    @media screen and (max-width: 767px) {
      float: right;
      padding-right: 10px;
    }
  }

  .tag-label {
    margin-top: 10px;
    padding-left: 15px;
    display: flex;
    align-items: center;

    @media screen and (min-width: 768px) {
      padding-left: 39px;
    }

    button,
    span {
      margin-left: 8px;
      font-weight: $font-weight-semibold;
      font-size: $font-size-smallest;

      color: $color-input-form-landing-desc2;
    }

    button {
      &:hover {
        text-decoration: underline;
      }
    }
  }
}