@import '../../../base';

.detail-collapse-container {
  margin-top: 30px;
  padding: 0;

  .collapse-button {
    width: 100%;
    background: white;
    box-shadow: 0px -2px 4px rgba(0, 0, 0, 0.05);
    border: none;

    display: flex;
    justify-content: center;
    align-items: center;

    .collapse-text {
      font-weight: $font-weight-bold;
      font-size: $font-size-extra-tiny;
      color: $color-input-form-landing-desc2;
      margin: 0 13px 0 0;
    }

    .rotate-arrow {
      transform: rotate(180deg);
    }

    img {
      width: 10px;
      height: auto;
      filter: $color-filter-hover;
      transition: transform 0.3s ease;
    }
  }

  hr {
    margin-top: 0 !important;
  }
}
