@import '../../../base';

.form-landing-splash-container {
  margin: 20% 0 5% 0;
  padding-right: 92px;
  .mas-container {
    display: flex;
    align-items: center;

    margin: 50px 0 20px 0;

    .h7 {
      position: relative;
      z-index: 1;
      max-width: 150px;
      margin-left: 10px;
      @media screen and (max-width: 1110px) {
        max-width: 120px;
      }
    }

    img {
      width: 100px;
      height: auto;

      filter: saturate(0%);
    }
  }

  h1 {
    position: relative;
    z-index: 1;
    max-width: 300px;
  }

  .splash-img {
    position: absolute;
    right: 0;
    bottom: 0;
    width: 256px;
  }
}

.form-landing-insurer-carousel {
  .insurer-logo-carousel {
    margin-top: 36px;

    .carousel-container {
      width: 112%;
      transform: translateX(-6%);
    }
    .insurer-logo-sq {
      height: 60px;

      img {
        object-fit: contain;
        width: 100px;
        height: 60px;
      }
    }
  }
}